/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

body {
  line-height: 1;
  font-family: "Roboto", sans-serif;
  position: relative;
  overflow-x: hidden;
}

ol,
ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

span {
  display: inline-block;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  border: 1px solid red;
}

header {
  width: 100%;
  max-width: 1250px;
  margin: 0 auto;
  height: 80px;
  padding-top: 30px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: fixed;
  left: 50%;
  transform: translateX(-50%);
}
header .menu-btn__wrapper {
  display: flex;
  align-items: center;
}
header .menu-btn__wrapper .text {
  color: #fff;
  margin-right: 20px;
}
header .menu-btn__wrapper .menu-btn {
  width: 16.38px;
  height: 2px;
  background-color: #4ba9ff;
  position: relative;
  cursor: pointer;
}
header .menu-btn__wrapper .menu-btn::before {
  content: "";
  width: 20px;
  height: 2px;
  background-color: #4ba9ff;
  position: absolute;
  top: -5px;
}
header .menu-btn__wrapper .menu-btn::after {
  content: "";
  width: 20px;
  height: 2px;
  background-color: #4ba9ff;
  position: absolute;
  top: 5px;
}

main {
  width: 100%;
  display: flex;
  background-color: #4822a8;
  border: 1px solid red;
  display: flex;
  justify-content: center;
  align-items: center;
}
main #hero {
  width: 100%;
  max-width: 1250px;
  margin: 0 auto;
  height: 120vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
}
main #hero .hero-text {
  font-size: 100px;
  font-weight: 300;
  color: #fff;
}
@media screen and (min-width: 769px) and (max-width: 1024px) {
  main #hero .hero-text {
    font-size: 8vw;
  }
}
@media screen and (min-width: 481px) and (max-width: 768px) {
  main #hero .hero-text {
    font-size: 8vw;
  }
}
@media screen and (max-width: 480px) {
  main #hero .hero-text {
    font-size: 8vw;
  }
}
main #hero .hero-text .inner-text {
  color: #4ba9ff;
}
main #hero .logo-text {
  font-size: 350px;
  position: absolute;
  bottom: 15%;
  color: #4822a8;
  text-shadow: 0px 5px 30px #271459;
  text-transform: uppercase;
  letter-spacing: 100px;
}